.page-enter {
  opacity: 0.01;
  transform: scale(0);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 500ms;
}

.page-exit {
  opacity: 1;
  transform: scale(0);
}

.page-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 500ms;
}
