@import url(https://fonts.googleapis.com/css2?family=Fruktur&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://pro.fontawesome.com/releases/v5.10.0/css/all.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.page-enter {
  opacity: 0.01;
  transform: scale(0);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 500ms;
}

.page-exit {
  opacity: 1;
  transform: scale(0);
}

.page-exit-active {
  opacity: 0.01;
  transform: scale(1.1);
  transition: all 500ms;
}

main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
}

.card {
  background-color: #292a41;
  border-radius: 1em;
  cursor: pointer;
  font-family: "Bebas Neue", cursive;
  padding: 1.5em;
  margin: 1em 1.5em;
  width: 100%;
  max-width: 350px;
  transition: all 200ms ease-in-out;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.primary {
  box-shadow: 0px 0px 13px -5px #bec925, 5px 0 5px #292a41;
}

.primary:hover {
  box-shadow: 0px 10px 20px -7px #bec925, 5px 0 15px #292a41;
  transform: scale(1.05);
}

.primary .card-title {
  text-shadow: 0 5px 10px #bec925;
}

@keyframes appear-primary {
  0% {
    background: #bec925;
  }
  100% {
    background: #bec925;
  }
}

.primary .card-active {
  animation-name: appear-primary;
}

.primary .card-item:hover .card-title-item {
  text-shadow: 0 5px 10px #bec925;
}

.red {
  box-shadow: 0px 0px 13px -5px #ef0e39, 5px 0 5px #292a41;
}

.red:hover {
  box-shadow: 0px 10px 20px -7px #ef0e39, 5px 0 15px #292a41;
  transform: scale(1.05);
}

.red .card-title {
  text-shadow: 0 5px 10px #ef0e39;
}

@keyframes appear-red {
  0% {
    background: #ef0e39;
  }
  100% {
    background: #ef0e39;
  }
}

.red .card-active {
  animation-name: appear-red;
}

.red .card-item:hover .card-title-item {
  text-shadow: 0 5px 10px #ef0e39;
}

.blue {
  box-shadow: 0px 0px 13px -5px #0869ad, 5px 0 5px #292a41;
}

.blue:hover {
  box-shadow: 0px 10px 20px -7px #0869ad, 5px 0 15px #292a41;
  transform: scale(1.05);
}

.blue .card-title {
  text-shadow: 0 5px 10px #0869ad;
}

@keyframes appear-blue {
  0% {
    background: #0869ad;
  }
  100% {
    background: #0869ad;
  }
}

.blue .card-active {
  animation-name: appear-blue;
}

.blue .card-item:hover .card-title-item {
  text-shadow: 0 5px 10px #0869ad;
}

.card-title {
  font-size: 3em;
  text-align: center;
}

.card-title-item {
  padding: 0.25em;
  font-size: 1.7em;
}

.card-progress .card-bar {
  background: #161724;
  border: 1px solid #161724;
  border-radius: 2px;
  display: inline-block;
  min-height: 5px;
  min-width: 37px;
}

.card-active {
  animation-duration: 200ms;
  animation-iteration-count: infinite;
}

.card-bar-one {
  animation-delay: 200ms;
}

.card-bar-two {
  animation-delay: 400ms;
}

.card-bar-three {
  animation-delay: 600ms;
}

.card-bar-four {
  animation-delay: 800ms;
}

.card-bar-five {
  animation-delay: 1000ms;
}

.card-bar-six {
  animation-delay: 1200ms;
}

.card-bottom {
  background: #161724;
  min-height: 0.75em;
  margin: 0.5em auto;
  position: relative;
  top: 0;
  left: -1.5em;
  width: calc(350px);
}

@media screen and (min-width: 576px) {
  .card-bottom {
    border-radius: 0 0 3em 0;
    width: calc(350px + 1.5em);
  }
  .card-bottom::after {
    background-color: #161724;
    border-radius: 0 3em 0 0;
    content: "";
    display: table;
    min-height: 0.75em;
    min-width: 1.5em;
    position: absolute;
    top: -0.75em;
    right: 0;
    z-index: 5;
  }
  .card-bottom::before {
    background-color: #292a41;
    border-radius: 0 1.125em 1.125em 0;
    content: "";
    display: table;
    min-height: 0.75em;
    min-width: 1.5em;
    position: absolute;
    top: -0.75em;
    right: 0.75em;
    z-index: 10;
  }
}
